import { Component, OnInit } from '@angular/core';
import { MarkSixResult } from '../models/common.model';
import { Mark6Service } from '../services/mark6.service';
import { CommonService } from '../services/common.service';
import { Router, ActivatedRoute } from '@angular/router';
import { AlertController } from '@ionic/angular';

@Component({
  selector: 'app-multical',
  templateUrl: './multical.component.html',
  styleUrls: ['./multical.component.scss']
})
export class MultiCalComponent implements OnInit {

  private totalLegs: number = 0;
  totalBetNo: number;
  totalBets: number;
  amount10: number;
  amount5: number;
  matchNo: number;
  incldudeSpecialNo: boolean = false;

  loaded: boolean = false;

  public m6result: MarkSixResult;



  constructor(private m6service: Mark6Service, private commonService: CommonService,
    private _router: Router, private _activeRoute: ActivatedRoute,
    private alertCtrl: AlertController) { }

  ngOnInit() {
  }


  onTotalBetNoChange(val: number) {
    this.totalLegs = val;


    this.totalBets = null;
    this.amount10 = null;
    this.amount5 = null;

    if (val >= 6 && val <= 49) {
      this.totalBets = Math.round(this.m6service.nCr(val, 6));

      this.amount10 = this.totalBets * 10;
      this.amount5 = this.totalBets * 5;
    }
  }

  onCalClick() {

    this.m6result = new MarkSixResult();

    this.m6result.prize1Qty = '-';
    this.m6result.prize2Qty = '-';
    this.m6result.prize3Qty = '-';
    this.m6result.prize4Qty = '-';
    this.m6result.prize4Amount = '-';
    this.m6result.prize5Qty = '-';
    this.m6result.prize5Amount = '-';
    this.m6result.prize6Qty = '-';
    this.m6result.prize6Amount = '-';
    this.m6result.prize7Qty = '-';
    this.m6result.prize7Amount = '-';

    this.loaded = true;

    if (this.totalLegs < 6 || this.totalLegs > 49) {

      this.commonService.showAlert('Error', '購買字數只可輸入6-49');
    } else {
        if (this.matchNo > 0) {
          if (this.incldudeSpecialNo) {
              if (this.matchNo > 7) {
                this.commonService.showAlert('Error', '中獎字數不能超過7個字');
                  return;
              }
          } else {
            if (this.matchNo > 6) {
              this.commonService.showAlert('Error', '中獎字數不能超過6個字');
              return;
            }
          }

        let results = this.m6service.calMultipleChance(this.totalLegs, this.matchNo, this.incldudeSpecialNo);

        if (results != null && results.length > 0) {


          for (let obj of results) {
            let prize = obj;

            let prizeNo = prize['PRIZE'];
            let matchCount = prize['MATCH'];

            let pNo = Number(prizeNo);

            let pCnt = Number(matchCount);

            let pNoVal = String(matchCount);

            //int nval = BONUS[(pNo - 1)];
            let pNoValx = pNo - 1;
            let nval = this.m6service.calBonusAmount(Number(pNoValx));

            let namt = nval * Number(pCnt);

            let amtStr : string = '$' + this.m6service.formatNumber(namt);

            switch (prizeNo) {
              case 1:
                this.m6result.prize1Qty = pNoVal;
                break;

              case 2:
                this.m6result.prize2Qty = pNoVal;
                break;

                case 3:
                this.m6result.prize3Qty = pNoVal;
                break;

                case 4:
                this.m6result.prize4Amount = amtStr;
                this.m6result.prize4Qty = pNoVal;
                break;

                case 5:
                this.m6result.prize5Amount = amtStr;
                this.m6result.prize5Qty = pNoVal;
                break;

                case 6:
                this.m6result.prize6Amount = amtStr;
                this.m6result.prize6Qty = pNoVal;
                break;

                case 7:
                  this.m6result.prize7Amount = amtStr;
                  this.m6result.prize7Qty = pNoVal;
                break;
            }
          }

        }
      }
    }
  }


  onClearClick() {
    this.totalBetNo = null;
    this.totalBets = null;
    this.amount10 = null;
    this.amount5 = null;
    this.matchNo = null;
    this.incldudeSpecialNo = false;

    this.loaded = false;

    this.m6result = null;
  }
}
